<template>
  <div id="translateForm">
		<form class="container" id="transform" v-on:submit="formSubmit">
			<input class="ml-auto mr-auto col-6 form-control" type="text" v-model="textToTranslate" placeholder="Enter a Word">
			<select class="ml-auto mr-auto col-6 form-control" v-model="language">
				<option value="ru">Russian</option>
				<option value="es">Spanish</option>
				<option value="fr">French</option>
				<option value="zh">Chinese</option>
			</select>
			<br>
			<input class="btn btn-primary" type="submit" value="Translate">
		</form>
	</div>
</template>

<script>
export default {
  name: 'translateForm',
  data(){
	  return {
		  textToTranslate:'',
		  language:''
	  }
  },
  created() {
	  this.language = 'ru';
  },
  methods: {
	  formSubmit(e) {
		  this.$emit('formSubmit', this.textToTranslate, this.language);
		  e.preventDefault();
	  }
  }
}
</script>

<style>
</style>



