<template>
  <div class="text-center" id="app">
    <h1 style="margin-top: 50px;">Word Translator</h1>
    <h5>Powered By Vue.js</h5>
    <br>
    <TranslateForm v-on:formSubmit="translateText"></TranslateForm>
    <TranslateOutput v-text="translatedText"></TranslateOutput>
  </div>
</template>

<script>
import TranslateForm from './components/TranslateForm'
import TranslateOutput from './components/TranslateOutput'

export default {
  name: 'app',
  components: {
    TranslateForm,
    TranslateOutput
  },
  data: function() {
    return {
      translatedText:''
    }
  },
  methods: {
    translateText:function(text, language) {
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180727T001113Z.ab6a32b441eca3b0.19a21dcf8f4675a9b3e58e2e99b0289126ee5efc&lang='+language+'&text='+text)
      .then((response) => {
        this.translatedText = response.body.text[0];  
      });
    } 
  }
}
</script>

<style>

</style>



